  .Messages-list {
    padding: 20px 0;
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    list-style: none;
    padding-left: 0;
    flex-grow: 1;
    overflow: auto;
  }

  .Messages-message {
    display: flex;
    margin-top: 10px;
  }

  .Messages-message.currentMember {
    /*justify-content: flex-end;*/
    flex-direction: row-reverse;
    text-align: right;
  }

  .Message-content {
    display: inline-block;
  }

  .currentMember>.Message-content {
    align-items: flex-end;
  }

  .Messages-message>.avatar {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    display: inline-block;
    margin: 0 10px -10px;
  }

  .Message-content>.username {
    display: block;
    color: gray;
    font-size: 14px;
    padding-bottom: 4px;
  }

  .Message-content>.text {
    padding: 10px;
    max-width: 400px;
    margin: 0;
    border-radius: 12px;
    background-color: cornflowerblue;
    color: white;
    display: inline-block;
  }

  .currentMember>.Message-content .text {
    background-color: orangered;
  }